:root{
    --element-spacing: 1.5rem;
}

.top-nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0.5rem;
}

.left > * {
    margin-right: var(--element-spacing);
}

.right{
    display: flex;
    align-items: center;
}

.right > * {
    margin-left: var(--element-spacing);
}